const defaultPageSize = 10;

const SearchCondition = {
    myAudit: {
        waitAuditListCondition: {//
            pageIndex: 0,
            pageSize: defaultPageSize,
            listType: "myAudit_wait"
        },
        auditedListCondition: {//
            examineStatus: [],//[1],
            contentTypes: [],
            pageIndex: 0,
            pageSize: defaultPageSize,
            listType: "myAudit_audited"
        }
    },
    mySubmit: {//
        examineStatus: [1],
        contentTypes: [],
        pageIndex: 0,
        pageSize: defaultPageSize,
        listType: "mySubmit"
    },
    copyToMe: {//(1:,2:)
        status: [],
        pageIndex: 0,
        pageSize: defaultPageSize,
        listType: "copyToMe"
    }

}

export default SearchCondition;


