import { createAction } from 'redux-actions';
import * as actionTypes from '../constants/actionType';

//
export const setLoadingVisible = createAction(actionTypes.SET_SEARCH_LOADING);
//
export const getAuditList = createAction(actionTypes.GET_AUDIT_LIST);
export const openAuditDetail = createAction(actionTypes.OPEN_AUDIT_DETAIL);
export const closeAuditDetail = createAction(actionTypes.CLOSE_AUDIT_DETAIL);
export const getAuditDetail = createAction(actionTypes.GET_AUDIT_DETAIL);
export const saveCondition = createAction(actionTypes.SAVE_CONDITION);//
export const getNoReadCount = createAction(actionTypes.GET_NO_READ_COUNT);//
export const getBuildingDetail = createAction(actionTypes.GET_BUILDING_DETAIL);//
export const getBuildingShops = createAction(actionTypes.GET_BUILDING_SHOPS);//
//
export const saveAudit = createAction(actionTypes.SAVE_AUDIT);//
export const getAuditHistory = createAction(actionTypes.GET_AUDIT_HISTORY);//
//
export const changeMenu = createAction(actionTypes.CHANGE_MENU);
//
export const getUpdateRecordDetail = createAction(actionTypes.GET_UPDATE_RECORD_DETAIL);//


