import { BaseApiUrl, basicDataBaseApiUrl } from '../../constants/baseConfig';

const WebApiConfig = {
    audit: {
        myAudit: {
            getWaitAuditList: basicDataBaseApiUrl + 'examines/waiting',//
            getAuditedList: basicDataBaseApiUrl + "examines/list",//
        },
        mySubmit: basicDataBaseApiUrl + 'examines/submitlist',//
        coypToMe: basicDataBaseApiUrl + 'examinenotices/list',//
        passAudit: basicDataBaseApiUrl + 'examines/pass/',//
        rejectAudit: basicDataBaseApiUrl + 'examines/reject/',//
        getAuditHistory: basicDataBaseApiUrl + "examines/",//
        getNoReadCount: basicDataBaseApiUrl + 'examinenotices',//
        getUpdateRecordDetail: basicDataBaseApiUrl + 'updaterecord/',//
    },
    houseActive: {
        buildingDetail: basicDataBaseApiUrl + "buildings/",//
        buildingShops: basicDataBaseApiUrl + "Shops/search",//"Shops/list",//
    }
}
export default WebApiConfig;


